// services

.services__title {
  margin-bottom: 26px;
  font-size: 22px;
  text-align: center;
}

.services__wrap {
  padding: 0 16px;
  padding-top: 20px;
  padding-bottom: 63px;
  background-color: @page-light-blue;
}

.services__table {
  .wrapper();
}

@media (min-width: 768px) {
  .services {
    padding-bottom: 30px;
  }

  .services__title {
    margin-bottom: 32px;
    font-size: 32px;
    text-align: center;
  }

  .services__wrap {
    padding: 0 32px;
    padding-top: 45px;
    padding-bottom: 30px;
    background-color: @page-light-blue;
  }

  .services__table {
    .wrapper();
  }
}

@media (min-width: 1600px) {
  .services__wrap {
    padding-right: 0;
    padding-left: 0;
  }
}
