// Location

.location {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 20%;
  .mp-reset();
  list-style: none;
}

.location__link {
  padding-top: 16px;
  padding-bottom: 17px;
  font-size: 13px;
  color: @page-gray;
  text-decoration: none;
}

.location__link--city {
  position: relative;
  padding-left: 29px;
}

.location__icon {
  position: absolute;
  top: 10px;
  left: 0;
}

.location__link--phone {
  color: @page-red;
  font-family: "PlumbMedium", "Arial", sans-serif;
  font-size: 20px;
}

@media (min-width: 1600px) {
  .location {
    width: 17%;
  }
}
