// Carousel-adv

.carousel-adv {
  display: flex;
  justify-content: space-between;
  .mp-reset();
  list-style: none;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: linear;
}

.carousel-adv__link {
  display: none;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  width: 320px;
  min-height: 186px;
  padding: 58px;
  padding-bottom: 64px;
  text-decoration: none;
  color: @basic-black;
}

.carousel-adv__link:first-child {
  display: flex;
  width: 320px;
}

.carousel-adv__icon {
  margin-bottom: 36px;
}

.carousel-adv__title {
  font-size: 22px;
  line-height: 1.1;
  text-align: center;
}

@media (min-width: 690px) {
  .carousel-adv__link:nth-child(2) {
    display: flex;
  }
}

@media (min-width: 1024px) {
  .carousel-adv__link:nth-child(3) {
    display: flex;
  }
}

@media (min-width: 1280px) {
  .carousel-adv__link:nth-child(4) {
    display: flex;
  }
}

@media (min-width: 1280px) {
  .carousel-adv__link:nth-child(5) {
    display: flex;
  }
}
