// Mobile drop

.mobile-drop {
  display: none;
  width: 100%;
  min-height: 100vh;
  padding: 33px 15px;
  background-image: linear-gradient(180deg, #2995ec 0%, #075ca2 100%);
}

.mobile-drop__showed {
  display: block;
}

.mobile-drop__ref-list,
.mobile-drop__diag-list,
.mobile-drop__info-list {
  .mp-reset();
  list-style: none;
}

.mobile-drop__ref-link,
.mobile-drop__diag-link,
.mobile-drop__info-link {
  text-decoration: none;
  opacity: 0.6;

  &:hover {
    opacity: 1;
  }

  &:active {
    color: #075ca1;
  }
}

.mobile-drop__ref-list {
  min-height: 290px;
  text-align: center;
}

.mobile-drop__ref-item:not(:last-child) {
  margin-bottom: 16px;
}

.mobile-drop__ref-link {
  .font-shwc(16px; 1; 400; @basic-white);
}

.mobile-drop__diag-list {
  margin-bottom: 70px;
  text-align: center;
}

.mobile-drop__diag-item:not(:last-child) {
  margin-bottom: 12px;
}

.mobile-drop__info-list {
  text-align: center;
}

.mobile-drop__info-item:not(:last-child) {
  margin-bottom: 12px;
}

.mobile-drop__diag-link,
.mobile-drop__info-link {
  .font-shwc(20px; 1; 700; @basic-white);
}

.mobile-drop__info-link {
  font-size: 18px;
}

@media (min-width: 1280px) {
  .mobile-drop {
    display: none;
  }
}
