// footer

.footer {
  background-image: linear-gradient(180deg, #2995ec 0%, #075ca2 100%);
}

.footer__wrap {
  padding: 0 16px;
  padding-top: 39px;
  padding-bottom: 45px;
}

.footer__wrap-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 24px;
}

.footer__logo {
  display: block;
  margin-bottom: 55px;
}

.footer__about-list,
.footer__ref-list,
.footer__drivers-ref-list {
  display: none;
}

.footer__work-time {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 60px;
}

.footer__work-phone {
  position: relative;
  margin-bottom: 20px;
  padding-left: 34px;
  .font-shwc(24px; 1; 700; @basic-white);
  text-decoration: none;
}

.footer__work-icon {
  position: absolute;
  top: 0;
  left: 0;
}

.footer__work-hours {
  .font-shwc(16px; 1.3; 400; @basic-white);
  text-align: center;
}

.footer__copy {
  .font-shwc(14px; 1; 300; @basic-white);
  text-transform: uppercase;
}

@media (min-width: 690px) {
  .footer__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-right: 32px;
    padding-bottom: 30px;
    padding-left: 32px;
  }

  .footer__wrap-grid {
    width: 48%;
    padding: 0;
  }

  .footer__ref-list {
    display: block;
    width: 48%;
    .mp-reset();
    list-style: none;
  }

  .footer__ref-item {
    margin-bottom: 18px;
  }

  .footer__ref-item:last-child {
    margin-bottom: 0;
  }

  .footer__ref-link {
    .font-shwc(17px; 1; 700; @basic-white);
    text-decoration: none;
    opacity: 0.5;

    &:hover {
      opacity: 1;
    }
  }
}

@media (min-width: 768px) {
  .footer__wrap-grid {
    width: 32%;
  }

  .footer__ref-list {
    width: 31%;
  }

  .footer__ref-item {
    margin-bottom: 15px;
  }

  .footer__ref-link {
    font-size: 20px;
  }

  .footer__drivers-ref-list {
    display: block;
    width: 31%;
    .mp-reset();
    list-style: none;
  }

  .footer__drivers-ref-item {
    margin-bottom: 15px;
  }

  .footer__drivers-ref-link {
    color: @basic-white;
    font-size: 16px;
    font-weight: 700;
    text-decoration: none;
    opacity: 0.5;
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: linear;

    &:hover {
      opacity: 1;
    }
  }
}

@media (min-width: 1024px) {
  .footer__wrap-grid {
    justify-content: flex-start;
  }

  .footer__logo {
    margin-bottom: 34px;
  }

  .footer__about-list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 100%;
    .mp-reset();
    margin-bottom: 65px;
    list-style: none;
  }

  .footer__about-link {
    .font-shwc(16px; 1; 400; @basic-white);
    text-decoration: none;
    opacity: 0.5;

    &:hover {
      opacity: 1;
    }
  }

  .footer__work-time {
    justify-content: flex-start;
    margin-bottom: 30px;
  }

  .footer__work-hours {
    text-align: left;
  }
}

@media (min-width: 1280px) {
  .footer__wrap-grid {
    width: 20%;
  }

  .footer__ref-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 50%;
    height: 100px;
  }

  .footer__ref-item {
    margin-bottom: 0;
  }

  .footer__drivers-ref-list {
    width: 26%;
  }
}

@media (min-width: 1600px) {
  .footer__wrap {
    width: 1600px;
    .wrapper();
  }
}
