// map

.map {
  padding-top: 15px;
  padding-bottom: 30px;
}

.map__title {
  margin-bottom: 16px;
  text-align: center;
}

.map__frame {
  width: 100%;
  border: none;
}

@media (min-width: 768px) {
  .map {
    padding-top: 30px;
    padding-bottom: 42px;
  }

  .map__title {
    font-size: 32px;
    margin-bottom: 32px;
  }
}
