// Advantages

.advantages {
  position: relative;
  padding-top: 5px;
  padding-bottom: 30px;

  &::before {
    content: "";
    position: absolute;
    top: 70px;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 338px;
    background-color: @basic-ultra-light;
  }
}

.advantages__title {
  margin-bottom: 30px;
  text-align: center;
}

.advantages__wrap {
  .wrapper();
  width: 320px;
  // width: 640px;
  // width: 960px;
  // width: 1280px;
  overflow: hidden;
}

.advantages__button {
  position: absolute;
  top: calc(50% + 48px);
  left: 16px;
  width: 48px;
  height: 48px;
  .mp-reset();
  background-color: transparent;
  border: none;
  border-radius: 50%;
  cursor: pointer;

  &:disabled {
    opacity: 0.4;
  }
}

.advantages__button--next {
  left: auto;
  right: 16px;
  transform: rotate(-180deg);
}

@media (min-width: 690px) {
  .advantages__wrap {
    width: 640px;
  }
}

@media (min-width: 768px) {
  .advantages {
    padding-top: 15px;
  }

  .advantages__title {
    font-size: 30px;
  }
}

@media (min-width: 1024px) {
  .advantages__wrap {
    width: 960px;
  }
}

@media (min-width: 1280px) {
  .advantages__wrap {
    width: 1280px;
  }
}

@media (min-width: 1600px) {
  .advantages__wrap {
    width: 1600px;
  }
}
