// ref-for

.ref-for {
  padding-top: 30px;
  padding-right: 16px;
  padding-left: 16px;
}

.ref-for__title {
  width: 90%;
  .wrapper();
  margin-bottom: 28px;
  text-align: center;
  line-height: 1.28;
}

.ref-for__button {
  display: none;
}

.ref-for__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .mp-reset();
  list-style: none;
}

.ref-for__item {
  width: 100%;
  margin-bottom: 25px;
  padding-bottom: 25px;
}

.ref-for__item:last-child {
  margin-bottom: 0;
}

.ref-for__image {
  width: 100%;
  .mp-reset();
  margin-bottom: 19px;
}

.ref-for__subtitle {
  margin-bottom: 18px;
  .font-shwc(20px; 1.2; 700; @page-blue);
}

@media (min-width: 690px) {
  .ref-for__item {
    width: 48%;
  }
}

@media (min-width: 768px) {
  .ref-for {
    padding-top: 40px;
    padding-right: 32px;
    padding-left: 32px;
  }

  .ref-for__title {
    margin-bottom: 32px;
    font-size: 32px;
  }
}

@media (min-width: 1024px) {
  .ref-for {
    position: relative;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .ref-for__title {
    width: 95%;
    margin-bottom: 46px;
  }

  .ref-for__button {
    position: absolute;
    top: 50%;
    left: 16px;
    display: block;
    width: 48px;
    height: 48px;
    .mp-reset();
    border: none;
    background-color: transparent;

    &:hover {
      cursor: pointer;
    }
  }

  .ref-for__button--next {
    left: auto;
    right: 16px;
    transform: rotate(-180deg);
  }

  .ref-for__item {
    width: 24%;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

@media (min-width: 1600px) {
  .ref-for {
    width: 1600px;
    .wrapper();
  }
}
