// Registration

.reg {
  padding-top: 30px;
  padding-bottom: 25px;
}

.reg__title {
  min-height: 58px;
  margin-bottom: 22px;
  text-align: center;
  line-height: 1.3;
}

.reg__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 24px;
  padding: 0 16px;
  border-bottom: 1px solid @basic-light;
}

.reg__link {
  position: relative;
  padding-bottom: 24px;
  font-size: 18px;
  color: @page-gray;
  text-decoration: none;
}

.reg__link--current {
  color: @page-blue;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 4px;
    background-color: @page-blue;
  }
}

.reg__image {
  width: 100%;
  margin-bottom: 17px;
}

.reg__comission {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 17px;
  padding: 0 16px;
}

.reg__card {
  width: auto;
  min-height: auto;
  padding-top: 0;
  padding-bottom: 0;
  background-color: @basic-white;

  &:hover {
    transform: none;
    box-shadow: none;
  }
}

.reg__card:not(:last-child) {
  margin-bottom: 0;
}

.reg__card-wrap {
  margin-bottom: 0;
  padding: 0;
}

.reg__card-icon-holder {
  margin-bottom: 12px;
}

.reg__card-price {
  margin-bottom: 5px;
}

.reg__signup {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 0 16px;
}

.reg__signup-phone {
  position: relative;
  order: -1;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 35px;
}

.reg__signup-wrap {
  width: 65%;
  min-height: 92px;
  margin-left: auto;
  padding-top: 20px;
}

.reg__signup-phone-number {
  font-size: 24px;
  font-weight: 700;
  color: @page-red;
  text-decoration: none;
}

.reg__signup-text {
  font-size: 14px;
  line-height: 1.3;
  font-weight: 700;
}

@media (min-width: 768px) {
  .reg {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .reg__title {
    margin-bottom: 32px;
    font-size: 32px;
  }

  .reg__list {
    .wrapper();
    width: 78%;
    padding: 0 32px;
  }

  .reg__wrap-main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .reg__wrap {
    width: 48%;
  }

  .reg__comission {
    z-index: 55;
    width: 100%;
    padding-left: 32px;
    padding-right: 0;
  }

  .reg__card {
    width: 48%;
    padding: 0;
    padding-right: 30px;
  }

  .reg__signup {
    width: 100%;
    padding: 0 32px;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .reg__signup-button,
  .reg__signup-phone {
    width: 48%;
  }
}

@media (min-width: 1024px) {
  .reg {
    padding-top: 40px;
    padding-right: 32px;
    padding-bottom: 40px;
    padding-left: 32px;
  }

  .reg__list {
    padding: 0;
  }

  .reg__wrap {
    order: -1;
    width: 35%;
  }

  .reg__comission {
    padding-left: 0;
  }

  .reg__min {
    width: 32%;
  }

  .reg__card {
    order: 2;
    width: 337px;
    max-width: none;
    padding-right: 0;
  }

  .reg__signup {
    order: 3;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 60%;
    padding: 0;
    padding-top: 45px;
    margin-bottom: 0;
  }

  .reg__signup-button {
    width: 100%;
  }

  .reg__signup-phone {
    width: 70%;
    margin-bottom: 0;
  }
}

@media (min-width: 1280px) {
  .reg__wrap {
    width: 25%;
  }

  .reg__comission {
    width: 100%;
  }

  .reg__icon {
    width: 35px;
    height: 35px;
    top: 0;
  }

  .reg__card {
    order: 0;
    width: 20%;
  }

  .reg__signup {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-top: 0;
  }

  .reg__signup-wrap {
    padding-top: 0;
  }

  .reg__signup-button {
    width: 48%;
  }

  .reg__signup-phone {
    order: 0;
    align-items: center;
    width: 38%;
  }

  .reg__signup-image {
    width: 126px;
    height: 123px;
  }
}

@media (min-width: 1600px) {
  .reg {
    width: 1600px;
    .wrapper();
  }

  .reg__wrap {
    width: 27%;
  }

  .reg__text {
    width: 62%;
  }
}
