// Arrange

.arrange {
  // width: 320px;
  padding: 0 16px;
  padding-top: 20px;
  .wrapper();
}

.arrange__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.arrange__button {
  display: none;
}

@media (min-width: 768px) {
  .arrange {
    padding: 0 32px;
    padding-top: 27px;
  }
}

@media (min-width: 1280px) {
  .arrange {
    margin: 0;
    padding: 0;
    padding-top: 0;
  }

  .arrange__list {
    position: relative;
  }

  .arrange__button {
    position: absolute;
    top: 50%;
    left: -20px;
    z-index: 56;
    display: block;
    width: 40px;
    height: 40px;
    .mp-reset();
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition-property: transform;
    transition-duration: 0.3s;
    transition-timing-function: linear;

    &:hover {
      transform: scale(1.2);
    }
  }

  .arrange__button--next {
    right: -20px;
    left: auto;
    transform: rotate(-180deg);

    &:hover {
      transform: rotate(-180deg) scale(1.2);
    }
  }
}
