// price-table

.price-table {
  border-style: none;
  border-spacing: 0;
}

.price-table__row:first-child {
  display: table-row;
  margin-bottom: 0;

  &:hover {
    box-shadow: none;
  }
}

.price-table__row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  &:hover {
    box-shadow: 0 0 30px 10px rgba(28, 27, 75, 0.07);
  }
}

.price-table__row--mobile {
  display: none;
}

.price-table__head {
  display: none;
  color: @page-blue;
  font-size: 15px;
  text-align: left;
  font-weight: 400;
}

.price-table__head--name {
  padding-left: 40px;
}

.price-table__head--time,
.price-table__head--price,
.price-table__head--isOnline {
  text-align: center;
}

.price-table__data {
  padding: 10px;
  background-color: @basic-white;
}

.price-table__data--name {
  width: 100%;
  min-height: 112px;
  padding-top: 14px;
  padding-right: 45px;
  padding-left: 45px;
  text-align: center;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.price-table__data--time,
.price-table__data--price {
  position: relative;
  padding-top: 35px;
  padding-bottom: 24px;
  width: 50%;
  color: @page-pink;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
}

.price-table__data--time {
  border-bottom-left-radius: 20px;
}

.price-table__data--price {
  border-bottom-right-radius: 20px;
}

.price-table__time-mobile,
.price-table__price-mobile {
  position: absolute;
  display: block;
  top: 12px;
  left: calc(50% - 106px / 2);
  font-size: 14px;
  font-weight: 400;
  color: @page-blue;
}

.price-table__price-mobile {
  left: calc(50% - 66px / 2);
}

.price-table__data--isOnline {
  position: relative;
  display: none;
  // width: 17%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.price-table__hover {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px;
  color: @basic-black;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  background-color: @page-yellow;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.price-table__data--isOnline:hover .price-table__hover {
  display: flex;
}

.price-table__icon {
  .wrapper();
}

@media (min-width: 768px) {
  .price-table {
    width: 100%;
    border-spacing: 0 15px;
  }

  .price-table__row {
    display: table-row;
    flex-wrap: nowrap;
    margin-bottom: 0;
  }

  .price-table__head {
    display: table-cell;
    font-size: 13px;
  }

  .price-table__data--name {
    width: 55%;
    min-height: auto;
    font-size: 13px;
    text-align: left;
    border-top-right-radius: 0;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
  }

  .price-table__data--time,
  .price-table__data--price {
    position: relative;
    padding-top: 0;
    padding-bottom: 0;
    width: auto;
    font-size: 13px;
    font-weight: 400;
  }

  .price-table__data--time {
    border-bottom-left-radius: 0;
  }

  .price-table__data--price {
    border-bottom-right-radius: 0;
  }

  .price-table__time-mobile,
  .price-table__price-mobile {
    display: none;
  }

  .price-table__data--isOnline {
    display: table-cell;
    width: 17%;
  }
}

@media (min-width: 1024px) {
  .price-table__head {
    font-size: 15px;
  }

  .price-table__data--name {
    font-size: 15px;
  }

  .price-table__data--time,
  .price-table__data--price {
    font-weight: 700;
    font-size: 15px;
  }

  .price-table__hover {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

@media (min-width: 1280px) {
  .price-table__head {
    font-size: 17px;
  }

  .price-table__data--name {
    font-size: 17px;
  }

  .price-table__data--time,
  .price-table__data--price {
    font-weight: 700;
    font-size: 17px;
  }

  .price-table__hover {
    font-size: 17px;
  }
}

@media (min-width: 1600px) {
  .price-table {
    width: 1600px;
    padding: 0 32px;
  }
}
