// getapp-form component

.getapp-form {
  padding: 0 17px;
  padding-top: 19px;
  padding-bottom: 17px;
  background-color: @page-dark-blue;
  border-radius: 6px;
}

.getapp-form__wrap {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-bottom: 15px;
  line-height: 1;
}

.getapp-form__input,
.getapp-form__select,
.getapp-form__area {
  font-size: 16px;
  border: none;
}

.getapp-form__label {
  margin-bottom: 12px;
}

.getapp-form__input,
.getapp-form__select {
  min-height: 40px;
  padding: 11px;
  border-radius: 2px;
  background-color: @basic-white;
}

.getapp-form__select--ref {
  min-height: 90px;
  resize: none;
}

.getapp-form__submit {
  min-height: 64px;
  .mp-reset();
  margin-bottom: 18px;
  font-size: 18px;
  background-color: @page-yellow;

  &:hover {
    background-color: transparent;
    cursor: pointer;
  }
}

.getapp-form__wrap--lic {
  flex-direction: row;
  margin-bottom: 0;
}

.getapp-form__label--lic {
  position: relative;
  padding-left: 37px;
  font-size: 12px;
  line-height: 1.3;
  text-decoration-line: underline;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    border: 2px solid @page-blue;
    border-radius: 3px;
  }
}

.getapp-form__checkbox:checked ~ .getapp-form__label--lic::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  width: 17px;
  height: 10px;
  border: 2px solid @page-blue;
  border-top-style: none;
  border-right-style: none;
  transform: rotate(-45deg);
}

.getapp-form__checkbox:focus ~ .getapp-form__label--lic::before {
  outline: 2px solid darkorange;
}

@media (min-width: 768px) {
  .getapp-form__wrap-grid--form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .getapp-form__wrap {
    width: 32%;
    margin-bottom: 20px;
  }

  .getapp-form__wrap--textarea {
    width: 100%;
  }

  .getapp-form__select--ref {
    min-height: 40px;
  }

  .getapp-form__wrap-grid--submit {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  .getapp-form__submit {
    width: 54%;
    min-height: 40px;
    margin-bottom: 0;
    padding: 11px;
  }

  .getapp-form__wrap--lic {
    width: 40%;
    margin-bottom: 0;
  }
}

@media (min-width: 1024px) {
  .getapp-form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #97d0ff;
  }

  .getapp-form__wrap-grid--form {
    width: 68%;
  }

  .getapp-form__wrap-grid--submit {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 30%;
    padding-top: 18px;
  }

  .getapp-form__submit {
    width: 100%;
    min-height: 64px;
    margin-bottom: 45px;
  }

  .getapp-form__wrap--lic {
    width: auto;
  }
}
