// Card

.card {
  position: relative;
  min-height: 488px;
  width: 100%;
  margin-bottom: 50px;
  padding-top: 67px;
  padding-right: 18px;
  padding-bottom: 26px;
  padding-left: 18px;
  background-color: @basic-ultra-light;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: linear;

  &:hover {
    transform: scale(1.02);
    box-shadow: 2px 6px 30px 10px rgba(56, 55, 121, 0.19);
  }
}

.card:last-child {
  margin-bottom: 0;
}

.card__title {
  min-height: 50px;
  margin-bottom: 18px;
  font-size: 16px;
  line-height: 1.58;
}

@media (min-width: 690px) {
  .card {
    width: 48%;
  }

  .card:last-child {
    max-width: none;
    flex-grow: 3;
  }
}

.card__wrap {
  padding: 0 15px;
  margin-bottom: 40px;
}

.card__wrap--smaller {
  margin-bottom: 29px;
}

.card__special {
  position: absolute;
  top: 27px;
  right: calc(100% - 260px);

  .font-shwc(18px; 1; 700; @page-green);

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 14px;
    left: -44px;
    width: 15px;
    height: 3px;
    background-color: @page-green;
    transform: rotate(45deg);
  }

  &::after {
    top: 8px;
    left: -38px;
    width: 30px;
    transform: rotate(-45deg);
  }
}

.card__special--hide {
  display: none;
}

.card__title--bigger {
  margin-bottom: 15px;
  font-size: 18px;
}

.card__title--smaller {
  margin-bottom: 23px;
}

.card__icon-holder {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 25px;
}

.card__icon:not(:last-child) {
  margin-right: 10px;
}

.card__wrap--price {
  display: flex;
  flex-direction: column;
  margin-bottom: 23px;
  padding: 0;
}

.card__wrap--price-smaller {
  margin-bottom: 7px;
}

.card__text {
  font-size: 16px;
}

.card__price {
  color: @page-red;
  font-family: "PlumbMedium", "Arial", sans-serif;
  font-size: 30px;
}

.card__details {
  position: relative;
  .font-shwc(20px; 1; 400; @page-blue);
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    bottom: -2px;
    width: 100%;
    height: 2px;
    background-color: @page-blue;
  }
}

@media (min-width: 768px) {
  .card,
  .card:last-child {
    min-height: auto;
  }
}

@media (min-width: 1024px) {
  .card,
  .card:last-child {
    max-width: 32%;
    min-height: auto;
  }

  .card:not(:last-child) {
    margin-bottom: 0;
  }
}

@media (min-width: 1280px) {
  .card,
  .card:last-child {
    max-width: 355px;
    min-height: auto;
    flex-grow: 0;
  }

  .card:last-child {
    display: none;
  }

  .card__wrap {
    margin-bottom: 31px;
  }

  .card__title {
    font-size: 14px;
  }

  .card__placeref-btn {
    min-height: 40px;
    padding: 10px;
  }
}

@media (min-width: 1600px) {
  .card,
  .card:last-child {
    max-width: 280px;
  }

  .card:last-child {
    display: block;
  }
}
