// promo-banner

.promo-banner {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 464px;
    background-color: @page-pink;
  }
}

.promo-banner__wrap {
  padding-top: 22px;
  padding-right: 16px;
  padding-left: 16px;
  padding-bottom: 26px;
}

.promo-banner__wrap-content {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.promo-banner__image--abs {
  position: absolute;
  width: 256px;
  top: -4px;
  left: calc(43% - 256px / 2);
}

.promo-banner__text {
  width: 55%;
  min-height: 78px;
  margin-left: auto;
  margin-bottom: 187px;
  .font-shwc(22px; 1.2; 700; @basic-white);
}

.promo-banner__wrap-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  min-height: auto;
  margin-bottom: 21px;
}

.promo-banner__phone {
  display: block;
  margin-bottom: 12px;
  .font-shwc(34px; 1; 700; @basic-white);
  text-decoration: none;
}

.promo-banner__call-text {
  margin-bottom: 23px;
  .font-shwc(14px; 1.05; 700; @basic-white);
}

.promo-banner__button {
  border-color: @basic-white;
  color: @basic-white;
  align-self: center;
  width: 95%;
  min-height: 71px;
  padding-top: 20px;
  padding-bottom: 20px;

  &:hover {
    color: @basic-black;
    border-color: @basic-black;
    background-color: @basic-white;
  }
}

.promo-banner__image--car,
.promo-banner__image--cert {
  display: none;
}

@media (min-width: 768px) {
  .promo-banner {
    padding-top: 40px;

    &::before {
      height: 272px;
    }
  }

  .promo-banner__wrap {
    position: relative;
    padding-top: 35px;
    padding-right: 32px;
    padding-left: 32px;
    padding-bottom: 36px;
  }

  .promo-banner__wrap-content {
    margin-left: auto;
    width: 63%;
  }

  .promo-banner__image--abs {
    top: -19px;
    left: calc(-32% - 256px / 2);
  }

  .promo-banner__text {
    width: auto;
    min-height: auto;
    margin: 0;
    margin-bottom: 20px;
  }

  .promo-banner__wrap-grid {
    flex-direction: row;
    justify-content: flex-start;
    width: 64%;
    margin-bottom: 0;
  }

  .promo-banner__call-text {
    font-size: 15px;
  }

  .promo-banner__button {
    width: 258px;
  }

  .promo-banner__image--cert {
    position: absolute;
    top: 74px;
    right: 60px;
    display: block;
    width: 115px;
  }
}

@media (min-width: 1024px) {
  .promo-banner {
    padding-top: 60px;

    &::before {
      height: 190px;
    }
  }

  .promo-banner__wrap {
    padding-bottom: 48px;
  }

  .promo-banner__wrap-content {
    width: 73%;
  }

  .promo-banner__image--abs {
    top: -100px;
    left: calc(-19% - 256px / 2);
  }

  .promo-banner__wrap-grid {
    justify-content: space-between;
    width: 76%;
  }

  .promo-banner__call-text {
    margin-bottom: 0;
  }

  .promo-banner__button {
    width: 48%;
    min-height: auto;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .promo-banner__image--cert {
    top: 15px;
  }
}

@media (min-width: 1280px) {
  .promo-banner__image--car {
    position: absolute;
    display: block;
    top: calc(50% - 116px / 2);
    left: 42px;
    width: 200px;
    height: 116px;
  }

  .promo-banner__wrap-content {
    width: 60%;
  }

  .promo-banner__text {
    font-size: 32px;
  }
}

@media (min-width: 1600px) {
  .promo-banner__wrap {
    width: 1600px;
    .wrapper();
  }

  .promo-banner__image--car {
    top: calc(50% - 150px / 2);
    left: 140px;
    width: 260px;
    height: 150px;
  }

  .promo-banner__wrap-content {
    width: 47%;
    margin-right: 100px;
  }
}
