// Med-ref

.med-reference {
  .wrapper();
  padding: 0 16px;
  padding-top: 17px;
  padding-bottom: 35px;
}

.med-reference__title {
  margin-bottom: 8px;
  padding: 0 10px;
  line-height: 1.3;
  text-align: center;
}

.med-reference__wrap--closest {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}

.med-reference__label {
  margin-bottom: 12px;
  .font-shwc(16px; 1; 400; @basic-black);
}

.med-reference__select {
  width: 100%;
  min-height: 56px;
  margin-bottom: 19px;
  padding-top: 12px;
  padding-right: 10px;
  padding-bottom: 15px;
  padding-left: 9px;
  font-size: 16px;
  border: none;
  background-color: #eef2f7;
}

.med-reference__wrap--time {
  position: relative;
  flex-direction: row;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .med-reference {
    padding: 0 32px;
    padding-top: 24px;
    padding-bottom: 18px;
  }

  .med-reference__title {
    margin-bottom: 32px;
    font-size: 32px;
  }

  .med-reference__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .med-reference__wrap--closest {
    width: 48%;
  }

  .med-reference__wrap--time {
    padding: 0;
    padding-top: 20px;
  }

  .med-reference__label {
    margin-bottom: 12px;
    font-size: 16px;
  }

  .med-reference__select {
    margin-bottom: 0;
  }
}

@media (min-width: 1280px) {
  .med-reference {
    margin: 0;
    padding: 0;
    margin-bottom: 7px;
  }

  .med-reference__title {
    margin-bottom: 10px;
    padding: 0;
    font-size: 17px;
    text-align: left;
  }

  .med-reference__wrap--closest {
    align-items: stretch;
  }

  .med-reference__wrap--time {
    padding-top: 17px;
  }

  .med-reference__label {
    margin-bottom: 5px;
    font-size: 12px;
  }

  .med-reference__select {
    padding: 10px;
  }
}

@media (min-width: 1600px) {
  .med-reference__title {
    margin-bottom: 8px;
    font-size: 32px;
  }

  .med-reference__label {
    margin-bottom: 15px;
    font-size: 16px;
  }
}
