// Info

.info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  .mp-reset();
  list-style: none;
  width: 38%;
}

.info__link {
  display: block;
  padding-top: 16px;
  padding-bottom: 17px;
  .font-shwc(13px; 1; 700; @page-gray);
  text-decoration: none;
  text-transform: uppercase;
}

.info__link--current {
  color: @page-blue;
  border-bottom: 4px solid @page-blue;
}

.info__link--alert {
  color: @page-pink;
}

@media (min-width: 1600px) {
  .info {
    width: 35%;
  }
}
