// Papers

.papers {
  padding: 0 16px;
  padding-top: 8px;
}

.papers__title {
  margin-bottom: 22px;
  line-height: 1.35;
}

.papers__item--1 {
  margin-bottom: 36px;
}

.papers__list {
  .mp-reset();
  margin-bottom: 16px;
  padding-left: 16px;
  font-size: 16px;
  line-height: 1.21;
  list-style: none;
}

.papers__list-item {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 7px;
    left: -15px;
    width: 6px;
    height: 6px;
    background-color: @page-blue;
    border-radius: 50%;
  }
}

.papers__list-item:not(:last-child) {
  margin-bottom: 20px;
}

.papers__list-item--note {
  &::before {
    content: "";
    position: absolute;
    top: 3px;
    left: -13px;
    width: 2px;
    height: 100%;
    background-color: @page-pink;
    border-radius: 0;
  }
}

.papers__title--bottom {
  margin-bottom: 17px;
}

@media (min-width: 768px) {
  .papers {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 32px;
    padding-top: 16px;
    padding-bottom: 16px;
  }

  .papers__item {
    width: 48%;
    min-height: 292px;
  }

  .papers__item--1 {
    margin-bottom: 0;
  }
}

@media (min-width: 1024px) {
  .papers {
    width: 60%;
    order: 0;
    padding: 0;
  }

  .papers__item--2 {
    order: -1;
  }
}

@media (min-width: 1280px) {
  .papers {
    width: 50%;
    order: 0;
    padding: 0;
  }

  .papers__item--2 {
    order: 0;
  }

  .papers__list {
    margin-bottom: 0;
  }
}
