// wrapper

@media (min-width: 1280px) {
  .section-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .wrapper();
    padding: 0 32px;
  }

  .section-wrapper--grid {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 48%;
    margin: 0;
    padding: 0;
    padding-top: 10px;
  }
}

@media (min-width: 1600px) {
  .section-wrapper {
    width: 1600px;
  }

  .section-wrapper--grid {
    width: 56%;
    padding-top: 10px;
  }
}
