// Header

.header {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: @basic-light;
  }
}

.header__wrap--info {
  display: none;
}

.header__wrap--service {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  .wrapper();
  padding-top: 6px;
  padding-right: 16px;
  padding-bottom: 6px;
  padding-left: 16px;
}

.header__toggle {
  .mp-reset();
  background-color: transparent;
  border: none;
}

.header__reference {
  display: none;
}

.header__work-time {
  display: none;
}

.header__mobile-drop {
  position: absolute;
  z-index: 55;
  top: 60px;
  left: 0;
}

@media (min-width: 768px) {
  .header__wrap--service {
    padding-top: 12px;
    padding-right: 32px;
    padding-bottom: 12px;
    padding-left: 34px;
  }

  .header__logo {
    order: -1;
  }

  .header__mobile-drop {
    top: 70px;
  }
}

@media (min-width: 1280px) {
  .header__underline {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: @basic-light;
    }
  }

  .header__wrap--info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .wrapper();
    padding: 0 32px;
  }

  .header__wrap--service {
    padding: 0 32px;
  }

  .header__toggle {
    display: none;
  }

  .header__mobile-phone {
    display: none;
  }

  .header__reference {
    display: flex;
  }

  .header__work-time {
    display: flex;
  }
}

@media (min-width: 1600px) {
  .header__wrap--info {
    width: 1600px;
  }

  .header__wrap--service {
    width: 1600px;
  }
}
