//Navigarion

.navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 31%;
}

.navigation__link {
  display: block;
  padding-top: 16px;
  padding-bottom: 17px;
  color: @page-gray;
  font-size: 13px;
  text-decoration: none;
}

.navigation__link--current {
  color: @page-red;
}

@media (min-width: 1600px) {
  .navigation {
    width: 33%;
  }
}
