// Ref

.reference {
  display: none;
}

@media (min-width: 1280px) {
  .reference {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 88%;
    .mp-reset();
    list-style: none;
  }

  .reference__item {
    position: relative;
    width: 25%;
    border-left: 1px solid @basic-light;
  }

  .reference__link {
    display: block;
    position: relative;
    min-height: 66px;
    padding-top: 20px;
    padding-right: 30px;
    padding-left: 65px;
    padding-bottom: 10px;
    color: @basic-black;
    font-size: 15px;
    text-decoration: none;
    text-align: center;
  }

  .reference__link--worktime {
    font-size: 12px;
    text-align: right;
  }

  .reference__icon {
    position: absolute;
    top: 15px;
    left: 25px;
  }

  .reference__dropdown {
    position: absolute;
    z-index: 55;
    display: none;
    width: 100%;
    min-height: 491px;
    .mp-reset();
    background-color: @basic-white;
    list-style: none;
    box-shadow: 2px 6px 30px rgba(56, 55, 121, 0.19);
  }

  .reference__dropdown-item {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 70px;
    padding-right: 35px;
    border-bottom: 1px solid @basic-light;
  }

  .reference__dropdown-icon {
    position: absolute;
    top: calc(50% - 24px / 2);
    right: 15px;
    display: none;
  }

  .reference__dropdown-item--hasdrop .reference__dropdown-icon {
    display: block;
  }

  .reference__dropdown-link {
    display: block;
    padding-top: 10px;
    padding-right: 25px;
    padding-left: 25px;
    padding-bottom: 10px;
    .font-shwc(16px; 1.3; 400; @basic-black);
    text-decoration: none;

    &:hover {
      color: @page-pink;
      text-decoration: underline;
    }
  }

  .reference__item--dropdown-1:hover > .reference__dropdown {
    display: block;
  }

  .reference__dropdown--sub {
    display: none;
    position: absolute;
    z-index: 54;
    top: 0;
    left: 100%;
    min-height: auto;
  }

  .reference__dropdown--sub .reference__dropdown-item {
    padding-right: 0;
  }

  .reference__dropdown-item--hasdrop:hover .reference__dropdown--sub {
    display: block;
  }
}
