// Button

.button {
  display: block;
  width: 100%;
  min-height: 83px;
  padding: 27px 20px;
  font-family: "PlumbMedium", "Arial", sans-serif;
  .font-shwc(20px; 1; 400; @basic-black);
  text-align: center;
  text-decoration: none;
  border: 2px solid @page-yellow;
  border-radius: 6px;

  &:hover {
    background-color: @page-yellow;
  }

  &:active {
    opacity: 0.6;
  }
}
