// About

.about-company {
  position: relative;
  // width: 320px;
  .wrapper();
}

.about-company__license {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
  padding-right: 52px;
  padding-bottom: 4px;
  padding-left: 48px;
}

.about-company__desc {
  // width: 161px;
  width: 73.5%;
  min-height: 45px;
  .font-shwc(14px; 15px; 400; @basic-black);
}

.about-company__count {
  position: absolute;
  top: 80px;
  left: 16px;
  width: 112px;
  height: 112px;
  padding: 20px;
  text-align: center;
  background-color: @page-red;
  border-radius: 50%;
  .font-shwc(12px; 1; 400; @basic-white);
}

.about-comany__bigger {
  .font-shwc(50px; 1; 700; @basic-white);
}

.about-company__image--employee {
  width: 100%;
}

.about-company__image--disc {
  position: absolute;
  bottom: 17px;
  right: 16px;
}

.about-company__note {
  display: none;
}

@media (min-width: 768px) {
  .about-company__license {
    position: absolute;
    top: 33px;
    left: 50px;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 184px;
    min-height: 121px;
    padding: 0;
  }

  .about-company__image--license {
    margin-bottom: 10px;
  }

  .about-company__desc {
    width: auto;
    text-align: center;
  }

  .about-company__count {
    top: 30px;
    left: auto;
    right: 30px;
    width: 150px;
    height: 150px;
    font-size: 18px;
  }

  .about-comany__bigger {
    font-size: 60px;
  }

  .about-company__image--disc {
    right: auto;
    left: 87px;
    width: 153px;
    height: 153px;
  }

  .about-company__note {
    position: absolute;
    bottom: 30px;
    right: 30px;
    display: block;
    width: 381px;
    min-height: 129px;
    padding-top: 17px;
    padding-right: 27px;
    padding-left: 27px;
    .font-shwc(16px; 1.1; 400; @page-red);
    background-color: @basic-white;
    border-radius: 6px;
  }
}

@media (min-width: 1280px) {
  .about-company {
    width: 50%;
    margin: 0;
  }

  .about-company__license {
    top: 60px;
  }

  .about-company__image--employee {
    width: 100%;
  }

  .about-company__count {
    top: 50px;
    width: 120px;
    height: 120px;
    font-size: 12px;
  }

  .about-company__image--disc {
    bottom: 45px;
    left: 20px;
    width: 120px;
    height: 120px;
  }

  .about-company__note {
    bottom: 30px;
  }
}

@media (min-width: 1600px) {
  .about-company {
    width: 43.3%;
  }
}
