// Work time

.work-time {
  width: 200px;
  .font-shwc(12px; 1.5; 400; @basic-black);
  text-align: right;
}

.work-time__text {
  position: relative;
}

.work-time__icon {
  position: absolute;
  top: 4px;
  left: -20px;
}

@media (min-width: 1280px) {
  .work-time {
    width: 20%;
    padding-top: 10px;
  }

  .work-time__text {
    padding-left: 30px;
  }

  .work-time__icon {
    top: -2px;
  }
}
