// Ref-info

.ref-info {
  padding: 0 16px;
  padding-top: 20px;
  padding-bottom: 20px;
  .wrapper();
}

.ref-info--desktop {
  display: none;
}

.ref-info__title {
  margin-bottom: 15px;
  text-align: center;
  line-height: 1.3;
}

.ref-info__desc {
  font-size: 16px;
  line-height: 1.6;
}

.ref-info__dots--hidden {
  display: none;
}

.ref-info__more,
.ref-info__less {
  font-size: 16px;
  color: @page-blue;
}

.ref-info__more--hidden {
  display: none;
}

.ref-info__hidden {
  display: none;
}

.ref-info__hidden--showed {
  display: inline;
}

.ref-info__less {
  display: none;
}

.ref-info__less--show {
  display: inline;
}

@media (min-width: 768px) {
  .ref-info {
    padding: 0 32px;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .ref-info__title {
    margin-bottom: 32px;
    font-size: 32px;
  }
}

@media (min-width: 1024px) {
  .ref-info--desktop {
    display: block;
  }

  .ref-info__dots {
    display: none;
  }

  .ref-info__more {
    display: none;
  }

  .ref-info__hidden {
    display: inline;
  }
}

@media (min-width: 1600px) {
  .ref-info {
    width: 1600px;
  }
}
