// application

.application {
  padding: 0 16px;
  padding-bottom: 30px;
}

.application__title {
  margin-bottom: 18px;
  line-height: 1.28;
  text-align: center;
}

@media (min-width: 768px) {
  .application__title {
    margin-bottom: 32px;
    font-size: 32px;
  }
}

@media (min-width: 1600px) {
  .application {
    width: 1600px;
    .wrapper();
    padding-top: 30px;
  }
}
