// Comission

.comission {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}

.comission__text {
  position: relative;
  width: 67%;
  min-height: 36px;
  padding-left: 46px;
  font-size: 14px;
  line-height: 1.3;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 2px;
    height: 100%;
    background-color: @basic-light;
  }
}

.comission__icon {
  position: absolute;
  top: 6px;
  left: 8px;
}

.comission__min {
  display: block;
  width: 29%;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  color: @page-red;
}

.comission__int {
  font-size: 30px;
}

@media (min-width: 768px) {
  .comission {
    width: 48%;
  }

  .comission__text {
    font-size: 16px;
  }

  .comission__min {
    font-size: 24px;
  }

  .comission__int {
    font-size: 38px;
  }
}

@media (min-width: 1280px) {
  .comission__text {
    font-size: 14px;
  }

  .comission__min {
    font-size: 24px;
  }

  .comission__int {
    font-size: 24px;
  }
}
